.btn {
    border-radius: 50px;
    text-align: center;
    margin: 0 auto;
    cursor: pointer;
  }
  
  .card-cont {
    min-width: 240px;
    margin: 80px 0 40px 0;
    padding: 20px 5px;
    text-align: center;
    box-shadow: 0 15px 30px 0 rgba(0,0,0,.11), 0 5px 15px 0 rgba(0,0,0,.08);
    border-radius: 20px;  
    transition: all 0.3s ease;
}

.card-cont:hover {

    box-shadow: 0 15px 30px 0 rgba(0,0,0,.20), 0 5px 15px 0 rgba(0,0,0,.08);
}
  
  .img {
    max-width: 200px;
  }